.production {
  margin-bottom: 60px;
  &__title {
    margin-bottom: 30px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 500px 1fr;
    gap: 10px;

    @media (max-width: 992px) {
      display: none;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 13px 0 rgba(30, 55, 91, 0.14);
    background: var(--fon);
    padding: 20px;
    border: 1px solid var(--siniy);
    border-radius: 8px;

    .subtitle {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      margin-bottom: 16px;
    }
    span {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--siniy);
      display: block;
      margin-bottom: 8px;
    }
    p {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      margin-bottom: 30px;
    }

    .list {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
      margin-bottom: 20px;
      li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--chernyy);
        box-shadow: 0 4px 13px 0 rgba(30, 55, 91, 0.14);
        background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
        border-radius: 8px;
        padding: 4px 16px;
      }
    }
  }

  .btn--accent {
    max-width: 285px;
    padding: 10px 20px;
  }

  .prodction__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;

    .image {
      overflow: hidden;
      border-radius: 8px;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        border-radius: 8px;
        transition: scale 0.3s ease;
      }

      &__title {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: var(--belyy);
        position: absolute;
        bottom: 20px;
        left: 20px;
      }

      @media (any-hover: hover) {
        &:hover {
          img {
            scale: 1.1;
          }
        }
      }
    }
  }

  .production-mobile {
    display: none;

    @media (max-width: 992px) {
      display: grid;
      gap: 10px;
    }

    &__cart {
      display: flex;
      flex-direction: column;
      gap: 10px;
      position: relative;
      box-shadow: 0 1px 13px 0 rgba(30, 55, 91, 0.25);
      background: var(--fon);
      border-radius: 8px;
      .image {
        position: relative;
        height: 180px;
        overflow: hidden;
        border-radius: 8px;
        img {
          width: 100%;
        }
        .image-title {
          position: absolute;
          bottom: 20px;
          left: 20px;
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 18px;
          line-height: 120%;
          text-transform: uppercase;
          color: var(--belyy);
        }
      }

      .content {
        padding: 20px;
        display: flex;
        flex-direction: column;

        span {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 12px;
          color: var(--siniy);
          display: block;
          margin-bottom: 10px;
        }

        p {
          margin: 0;
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
          margin-bottom: 20px;
        }

        .btn {
          background: var(--siniy);
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 5px;
          padding: 12px;
          border-radius: 8px;
        }
      }
    }
  }
}
