.company {
  margin-bottom: 60px;

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: clamp(20px, 2vw, 40px);
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__images {
    display: grid;
    grid-template-columns: 540px 320px;
    gap: 10px;
    @media (max-width: 1344px) {
      grid-template-columns: 1fr 1fr;
    }

    img {
      border-radius: 10px;
    }
  }

  &__title {
    font-size: 28px;
    margin-bottom: 8px;
  }

  .desc {
    margin-bottom: clamp(30px, 3vw, 60px);
    flex: 1 1 auto;
  }
  p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin: 0;
    margin-bottom: 20px;
  }

  .list {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    @media (max-width: 767px) {
      flex-direction: column;
    }
    li {
      display: flex;
      flex-direction: column;
      gap: 4px;

      .number {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: clamp(32px, 3vw, 50px);
        line-height: 100%;
        text-transform: uppercase;
        color: var(--chernyy);

        span {
          font-size: 18px;
          line-height: 120%;
        }
      }

      p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        color: var(--chernyy);
      }
    }
  }

  &__images {
    img {
      height: 100%;
    }
  }
}
