.callback {
  margin-bottom: 60px;

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(24px, 3vw, 32px);
    text-transform: uppercase;
    color: var(--chernyy);
    margin-bottom: 10px;
  }

  p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin: 0;
    margin-bottom: 24px;
  }

  &__wrapper {
    border-radius: 8px;
    padding: 30px 40px;
    background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
    position: relative;

    @media (max-width: 767px) {
      padding: 20px 10px;
      padding-bottom: 0;
    }
  }

  &__inner {
    @media (max-width: 767px) {
      display: grid;
      gap: 20px;
    }
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 160px;
    max-width: 441px;

    @media (max-width: 1440px) {
      right: 10px;
      max-width: 350px;
    }
    img {
      display: block;
    }

    @media (max-width: 767px) {
      position: static;
      max-width: 100%;
    }
  }

  .callback-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 710px;
    position: relative;
    z-index: 1;
    &__input {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;

      input {
        flex: 1 1 300px;
      }
    }

    &__bottom {
      margin-top: 20px;
      display: flex;
      align-items: center;
      gap: 20px;
      flex-wrap: wrap;
      .policy {
        display: flex;
        flex-direction: column;
        gap: 8px;
        max-width: 325px;
        width: 100%;
        .policy-block {
          position: relative;
          .checkbox {
            position: absolute;
            opacity: 0;
            appearance: none;
            width: 100%;
            height: 100%;
            cursor: pointer;
            z-index: 1;
          }
          label {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            color: var(--chernyy);
            position: relative;
            display: block;
            padding-left: 24px;
            a {
              text-decoration: underline;
              text-decoration-skip-ink: none;
              color: inherit;
              position: relative;
              z-index: 2;
            }

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 14px;
              height: 14px;
              border-radius: 2px;
              border: 1px solid var(--siniy);
              position: absolute;
              transition: 0.3s ease;
            }
          }

          .checkbox:checked + label:before {
            background: url("../../img/icons/checkbox.svg") no-repeat;
            border-color: transparent;
          }
        }
      }
      .btn--accent {
        border-radius: 8px;
        padding: 12px 40px;
        width: 100%;
      }
    }
  }
}
