.modal {
  position: fixed;
  inset: 0;
  display: grid;
  place-items: center;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(20px);
  overflow: auto;
  padding: 20px;
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;
  &._active {
    opacity: 1;
    visibility: visible;
  }
  &__container {
    max-width: 755px;
    box-shadow: 0 4px 13px 0 rgba(30, 55, 91, 0.14);
    background: var(--fon);
    position: relative;
    border-radius: 8px;
    padding: 40px 20px 20px;
    width: 100%;

    @media (max-width: 992px) {
      padding: 20px 10px;
    }
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    z-index: 2;
  }

  &__content {
    display: flex;
  }

  &__left {
    max-width: 420px;
    display: flex;
    flex-direction: column;
    @media (max-width: 992px) {
      max-width: 100%;
    }
  }

  &__title {
    margin-bottom: 10px;
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin-bottom: 30px;
  }

  .modal-form {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .modal-form-bottom {
      display: flex;
      flex-direction: column;
      gap: 10px;

      .policy {
        position: relative;
        input {
          opacity: 0;
          position: absolute;
          inset: 0;
          z-index: 1;
          appearance: none;
          cursor: pointer;
        }
        label {
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 12px;
          color: var(--chernyy);
          display: block;
          position: relative;
          padding-left: 20px;
          a {
            color: inherit;
            position: relative;
            z-index: 2;
            text-decoration: underline;
            text-decoration-skip-ink: none;
          }

          &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 14px;
            height: 14px;
            border-radius: 2px;
            border: 1px solid var(--siniy);
            position: absolute;
            -webkit-transition: 0.3s ease;
            transition: 0.3s ease;
          }
        }

        input:checked + label:before {
          background: url(../../img/icons/checkbox.svg) no-repeat;
          border-color: transparent;
        }
      }
    }
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 0;
    max-width: 310px;

    @media (max-width: 992px) {
      display: none;
    }
  }
}
