.seo-block {
  margin-bottom: 60px;
  &__title {
    margin-bottom: 30px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 490px 1fr;
    gap: 20px;

    @media (max-width: 1024px) {
      grid-template-columns: 1fr;
    }
    .map {
      border-radius: 8px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 8px;
      }
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      margin: 0;
      margin-bottom: 20px;
    }
    strong {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);

      margin-bottom: 8px;
    }

    ul {
      display: flex;
      flex-direction: column;
      gap: 8px;
      margin-bottom: 20px;
      li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
        position: relative;
        padding-left: 12px;

        &:before {
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          width: 6px;
          height: 6px;
          border-radius: 100%;
          background: var(--siniy);
        }
      }
    }
  }
}
