.tech {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: clamp(20px, 2.5vw, 40px);

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    @media (max-width: 992px) {
      display: none;
    }
  }

  .banner-section__btn {
    background: var(--siniy);
    color: #fff;
  }

  .content {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: 30px;
    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      color: var(--chernyy);
      margin: 0;
    }
  }

  &__steps {
    margin-top: 14px;
    margin-bottom: 24px;
    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      margin-bottom: 14px;
    }

    &-items {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 13px;

      @media (max-width: 992px) {
        grid-template-columns: 1fr;
      }
    }
    &-item {
      display: flex;
      flex-direction: column;
      gap: 10px;
      img {
        height: 300px;
        object-fit: cover;
        border-radius: 12px;
        display: block;
      }
      p {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--chernyy);
      }
    }
  }

  &__video {
    margin-top: 20px;
    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      margin-bottom: 14px;
    }
    &-wrapper {
      position: relative;
    }
    video {
      max-width: 100%;
      height: auto;
      object-fit: cover;
      border-radius: 12px;
    }
    .video-play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
      width: 60px;
      height: 60px;
      background: url("../../img/icons/button-pla.svg") center / cover no-repeat;
      z-index: 2;
      transition: scale 0.3s ease;
      border: none;

      @media (any-hover: hover) {
        &:hover {
          scale: 1.1;
        }
      }
    }
  }
}
