html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 370px;
  min-height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  background-color: var(--belyy);
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.hidden-sm {
  @media (max-width: 768px) {
    display: none !important;
  }
}

.btn--gray {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-radius: 8px;
  padding: 8px 20px;
  background: var(--fon);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--chernyy);
  transition: background-color 0.2s ease, color 0.2s ease;

  @include hover {
    &:hover {
      background: var(--siniy);
      color: var(--belyy);

      svg path {
        stroke: #fff;
      }
    }
  }
}

.input-search {
  border: 1px solid var(--divayder);
  border-radius: 8px;
  padding: 15px 20px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--vspomogatenye-elementy);
  outline: none;

  transition: 0.3s ease;

  &:focus {
    outline: 1px solid var(--siniy);
  }
}

.btn--accent {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--belyy);
  padding: 8px 20px;
  transition: 0.3s ease;
  background: var(--siniy);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  @include hover {
    &:hover {
      background: var(--oranzhevyy);
    }
  }
}

.title__section {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: clamp(22px, 3vw, 28px);
  text-transform: uppercase;
  color: var(--chernyy);
  margin: 0;
}

.input-form input,
textarea {
  border-radius: 8px;
  padding: 12px 24px;
  width: 100%;
  background: var(--belyy);
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 130%;
  color: var(--vspomogatenye-elementy);
  border: none;
  outline: none;
  resize: none;
  &:focus {
    outline: 1px solid orange;
  }
}

.aside-filter {
  border-radius: 8px;
  padding: 20px 10px;
  background: var(--fon);

  .aside-filter-toggler {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: var(--chernyy);
    position: relative;
    margin-bottom: 20px;
    cursor: pointer;
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: 20px;
      height: 16px;
      background: url("../img/icons/arrow-city.svg") no-repeat;
      background-size: contain;
    }
  }

  .aside-filter-options {
    display: flex;
    flex-direction: column;
    gap: 4px;

    .filter-option {
      border-radius: 8px;
      padding: 8px 14px;
      position: relative;
      input {
        opacity: 0;
        appearance: none;
        position: absolute;
        z-index: 1;
        width: 100%;
        height: 100%;
        margin: 0;
        cursor: pointer;
      }
      label {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        color: var(--chernyy);
        display: block;
        padding-left: 10px;
        margin-left: 5px;
        transition: 0.3s ease;
        &:before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          border-radius: 2px;
          border: 1px solid var(--vspomogatenye-elementy);
          width: 16px;
          height: 16px;
          transition: 0.3s ease;
          margin: 0;
        }
      }

      input:checked + label:before {
        background: url("../img/icons/checkbox.svg") center / cover no-repeat;
        border: none;
        left: 10px;
      }

      input:checked + label {
        padding-left: 20px;
      }

      &:has(input:checked) {
        background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
      }
    }
  }
}

.banner-section {
  position: relative;
  border-radius: 8px;
  padding: 20px;
  overflow: hidden;
  &__bg {
    z-index: -1;
    position: absolute;
    inset: 0;

    img {
      height: 100%;
      width: 100%;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 40px;
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
  }

  &__btn {
    border: 2px solid var(--siniy);
    border-radius: 8px;
    padding: 12px 40px;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: var(--chernyy);
    transition: background-color 0.3s ease, color 0.3s ease;
    &.active {
      background: var(--siniy);
      color: var(--belyy);
    }
    @include hover {
      &:hover {
        background: var(--siniy);
        color: var(--belyy);
      }
    }
  }

  &__select {
    position: relative;
    max-width: 375px;
    &:after {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 20px;
      width: 16px;
      height: 12px;
      background: url("../img/icons/arrow-city.svg") center / cover no-repeat;
    }
    select {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 130%;
      color: var(--chernyy);
      border: 1px solid var(--siniy);
      border-radius: 8px;
      padding: 14px 24px;
      width: 100%;
      background: transparent;
      appearance: none;
    }
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 100px;
    @media (max-width: 1140px) {
      display: none;
    }
    img {
      display: block;
    }
  }
}

.aside-menu {
  &-list {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  &-link {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    padding: 12px 20px;
    border-radius: 8px;
    background: var(--fon);
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
    transition: 0.3s ease;

    @media (any-hover: hover) {
      &:hover {
        background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
      }
    }
  }
}
