.expert {
  margin-bottom: 60px;

  &__title {
    margin-bottom: 30px;
    font-size: 28px;
  }

  &__wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
    .expert__item {
      .expert__image {
        height: 250px;
        @media (max-width: 992px) {
          height: 100%;
        }
      }
    }
  }

  &__item {
    position: relative;
    display: flex;
    flex-direction: column;
    border-radius: 8px;
    padding: 0px 0px 20px;
    background: var(--fon);
    &__link {
      position: absolute;
      inset: 0;
      z-index: 1;
    }

    .date {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--chernyy);
      padding-inline: 14px;
      margin-bottom: 10px;
    }

    .title {
      transition: color 0.2s ease;
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
      color: var(--chernyy);
      padding-inline: 14px;
    }

    @media (any-hover: hover) {
      &:hover {
        .expert__image img {
          scale: 1.1;
        }

        .title {
          color: var(--oranzhevyy);
        }
      }
    }
  }

  &__image {
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    margin-bottom: 14px;
    img {
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 8px;
      transition: 0.3s ease;
    }
  }
}
