.breadcrumbs {
  margin: 35px 0 20px 0;

  ul {
    display: flex;
    align-items: center;
    gap: 10px;
  }

  li {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 12px;
    color: var(--chernyy);
    transition: opacity 0.3s ease;
    display: flex;
    align-items: center;
    position: relative;
    gap: 10px;
    will-change: opacity;
    &:not(:last-child) {
      &:after {
        content: "/";
      }
    }
    a,
    span,
    p,
    strong {
      color: inherit;
      font-size: inherit;
      font-weight: inherit;
      font-family: inherit;
    }

    &:has(a) {
      @media (any-hover: hover) {
        &:hover {
          opacity: 0.5;
        }
      }
    }

    &:last-child {
      color: var(--siniy);
    }
  }
}
