.character {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: clamp(20px, 2.5vw, 40px);

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    @media (max-width: 992px) {
      display: none;
    }
  }
  .banner-section__btn {
    background: var(--siniy);
    color: #fff;
  }

  .character__table {
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
    font-family: var(--font-family);
  }

  .character__table thead,
  .character__table tbody,
  .character__table tr {
    display: contents;
  }

  .character__table th,
  .character__table td {
    border-radius: 8px;
    padding: 14px 20px;
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    background: var(--fon);
  }

  .character__table th {
    background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
    font-weight: bold;
    text-align: left;
  }

  .character__table ul {
    margin: 0;
    padding-left: 20px;
  }

  .character__table li {
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin-bottom: 5px;
  }
}
