.career {
  margin-bottom: 10px;

  &__items {
    display: grid;
    gap: 10px;
  }
}

.career-content {
  margin-bottom: 60px;
  &__item {
    border-radius: 8px;
    padding: 14px 20px;
    background: var(--fon);
    cursor: pointer;

    .head {
      display: flex;
      justify-content: space-between;
      gap: 20px;

      .left {
        display: flex;
        flex-direction: column;
        gap: 14px;

        .exp {
          border-radius: 8px;
          padding: 4px 16px;
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
          background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
          max-width: fit-content;
        }
      }
      .subtitle {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
        pointer-events: none;
      }

      .toggler {
        border: 1px solid var(--siniy);
        border-radius: 4px;
        min-width: 30px;
        height: 30px;
        display: grid;
        place-items: center;
        pointer-events: none;
        svg {
          transition: 0.3s ease;
        }
      }
    }

    .bottom {
      display: grid;
      grid-template-rows: 0fr;
      overflow: hidden;
      transition: 0.3s ease;

      .content {
        overflow: hidden;
        margin-top: 0;
        transition: 0.3s ease;
        display: grid;
        grid-template-columns: 1fr 400px;
        gap: 20px;
        align-items: flex-start;
        @media (max-width: 992px) {
          grid-template-columns: 1fr;
        }
        strong {
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 16px;
          line-height: 120%;
          color: var(--chernyy);
          margin-bottom: 14px;
          display: block;
        }

        ul {
          display: flex;
          flex-direction: column;
          gap: 8px;
          &:not(:last-child) {
            margin-bottom: 20px;
          }
          li {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 14px;
            line-height: 130%;
            color: var(--chernyy);
            position: relative;
            padding-left: 14px;
            &:before {
              content: "";
              position: absolute;
              top: 50%;
              transform: translateY(-50%);
              left: 0;
              width: 6px;
              height: 6px;
              background: var(--siniy);
              border-radius: 100%;
            }
          }
        }

        .content-right {
          border-radius: 8px;
          padding: 20px;
          box-shadow: 0 1px 13px 0 rgba(30, 55, 91, 0.25);
          background: var(--belyy);
          display: flex;
          flex-direction: column;

          .income {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 14px;
            line-height: 130%;
            color: var(--chernyy);
            margin-bottom: 4px;
          }
          .income-price {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: 20px;
            line-height: 120%;
            color: var(--chernyy);
            margin-bottom: 20px;
          }

          .btn--accent {
            padding: 12px 40px;
            margin-bottom: 10px;
            display: block;
          }

          .btn--gray {
            padding: 12px 40px;
            background: transparent;
            border: 1px solid var(--siniy);

            @include hover {
              &:hover {
                background: var(--siniy);
                color: #fff;
              }
            }
          }
        }
      }
    }
  }

  .career-content__item--active {
    .bottom {
      grid-template-rows: 1fr;
    }
    .content {
      margin-top: 30px;
    }

    .toggler svg {
      rotate: 45deg;
    }
  }
}
