.projects {
  position: relative;
  margin-bottom: 60px;

  @media (max-width: 767px) {
    height: 700px;
  }

  .container {
    @media (max-width: 767px) {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  &__title {
    text-align: center;
  }
  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: -1;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 14px;
    padding: 98px 0;
    max-width: 590px;
    margin-left: 130px;
    @media (max-width: 1200px) {
      margin-left: 0;
      margin-right: auto;
    }
    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 14px;
      line-height: 130%;
      text-align: center;
      color: var(--chernyy);
    }
  }

  &__btn {
    max-width: 240px;
    margin: 0 auto;
    @media (max-width: 767px) {
      max-width: 100%;
      width: 100%;
    }
  }
}
