.footer {
  background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
  padding-block: 40px;
  border-top: 1px solid var(--divayder);

  &__logo {
    display: block;
    margin-bottom: 20px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;

    @media (max-width: 767px) {
      display: grid;
    }
  }

  .address {
    display: flex;
    flex-direction: column;
    gap: 14px;

    strong {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
    }

    p {
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 12px;
      color: var(--chernyy);
      max-width: 290px;
      position: relative;
      padding-left: 18px;
      margin: 0;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 14px;
        height: 14px;
        background: url("../img/icons/map.svg") center center no-repeat;
      }
      span {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        color: var(--vspomogatenye-elementy);
        display: block;
        margin-top: 5px;
      }
    }
  }

  &__nav {
    display: flex;
    flex-direction: column;
    gap: 8px;

    &-title {
      font-family: var(--font-family);
      font-weight: 700;
      font-size: clamp(14px, 2vw, 18px);
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
      padding-inline: 4px;
    }

    &-list {
      display: flex;
      flex-direction: column;
      gap: 4px;

      a,
      span {
        font-family: var(--font-family);
        font-weight: 600;
        font-size: 14px;
        color: var(--chernyy);
        display: flex;
        align-items: center;
        gap: 6px;
        transition: background-color 0.3s ease;
        border-radius: 4px;
        padding: 4px;
        @include hover {
          &:hover {
            background-color: var(--belyy);
          }
        }
      }
    }

    .link-sublist:has(.sublist) {
      cursor: pointer;
      span {
        pointer-events: none;
      }
      .sublist {
        display: none;
        flex-direction: column;
        gap: 2px;
        margin-top: 10px;
        &.vis {
          display: flex;
        }
        a {
          font-weight: 400;
        }
      }
    }

    .link-sublist:has(.vis) {
      span svg {
        rotate: 180deg;
      }
    }
  }

  .footer-menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 20px;
    max-width: 790px;
    width: 100%;

    @media (max-width: 767px) {
      grid-template-columns: 1fr;
      order: 3;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__phone {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    text-transform: uppercase;
    color: var(--chernyy);
    transition: color 0.3s ease;

    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  &__mail {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    transition: color 0.3s ease;
    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  &__socials {
    display: flex;
    gap: 5px;

    a {
      transition: opacity 0.3s ease;
      will-change: opacity;

      @include hover {
        &:hover {
          opacity: 0.7;
        }
      }
    }
  }

  &__btn {
    border-radius: 8px;
    padding: 12px 40px;
    margin-top: 10px;
  }
}
