.header {
  padding-top: 16px;
  position: relative;
  z-index: 2;
  &__top {
    &-wrapper {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 20px;
    }

    &-left {
      display: flex;
      align-items: center;
      gap: 4px;
    }

    &-right {
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }

  .language {
    border-radius: 4px;
    padding: 8px;
    background: var(--fon);
    display: flex;
    flex-direction: column;
    gap: 3px;
    position: relative;
    button {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
      align-items: center;
      gap: 4px;
      transition: 0.3s ease;
      display: flex;
      &:before {
        content: "";
        width: 21px;
        height: 15px;
        background: url("../../img/icons/rus-flag.svg") no-repeat;
      }
    }
  }

  &__city {
    position: relative;
    select {
      background: #000;
      background: var(--fon);
      border-radius: 4px;
      padding: 8px 14px;
      border: none;
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
      appearance: none;
      width: 120px;
    }

    &:after {
      content: "";
      width: 10px;
      height: 6px;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 10px;
      background: url("../../img/icons/arrow-city.svg") no-repeat;
    }
  }

  &__search {
    max-width: 500px;
    width: 100%;
    position: relative;
    @media (max-width: 1344px) {
      max-width: 320px;
    }
    input {
      width: 100%;
    }

    .btn-search {
      position: absolute;
      width: 40px;
      height: 40px;
      display: grid;
      place-items: center;
      background: var(--fon);
      border-radius: 4px;
      top: 5px;
      right: 5px;
      transition: 0.3s ease;

      @include hover {
        &:hover {
          background: var(--siniy);

          svg path {
            fill: #fff;
          }
        }
      }
    }
  }

  &__phone {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(16px, 1.2vw, 18px);
    line-height: 120%;
    text-transform: uppercase;
    color: var(--chernyy);
    transition: color 0.3s ease;

    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  &__socials {
    display: flex;
    gap: 5px;
    align-items: center;
    a {
      transition: opacity 0.3s ease;
      @include hover {
        &:hover {
          opacity: 0.5;
        }
      }
    }
  }
  &__mail {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    transition: color 0.3s ease;

    @include hover {
      &:hover {
        color: var(--oranzhevyy);
      }
    }
  }

  .center {
    padding-block: 13px;

    &__wrapper {
      display: flex;
      gap: 60px;
      align-items: center;
    }

    .logo {
      display: flex;
      align-items: center;
      gap: 10px;

      span {
        font-size: 11px;
        font-weight: bold;
        line-height: 110%;
        color: var(--siniy);
        max-width: 77px;
      }
    }

    &__links {
      display: flex;
      gap: 10px;
      max-width: 785px;
      margin-left: 22px;
      a {
        font-family: var(--second-family);
        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        text-align: center;
        color: var(--chernyy);
        border: 1px solid var(--compoglass);
        border-radius: 8px;
        padding: 4px 40px;
        transition: 0.3s ease;
        @media (max-width: 1344px) {
          padding: 4px 20px;
        }
        @include hover {
          &:hover {
            background: var(--compoglass);
            color: var(--belyy);
          }
        }
        &.blue {
          border: 1px solid var(--siniy);
          @include hover {
            &:hover {
              background: var(--siniy);
              color: var(--belyy);
            }
          }
        }
        &.orange {
          border: 1px solid var(--oranzhevyy);
          @include hover {
            &:hover {
              background: var(--oranzhevyy);
              color: var(--belyy);
            }
          }
        }
      }
    }
  }

  .sublist__wrapper {
    position: absolute;
    top: 60px;
    z-index: 2;
    width: 365px;
    display: none;
    .sublist {
      display: flex;
      flex-direction: column;
      gap: 4px;
      &__item {
        position: relative;
        display: flex;
        align-items: flex-start;
        gap: 20px;
        width: 100%;
        &:after {
          content: "";
          position: absolute;
          width: 20px;
          height: 100%;
          background: transparent;
          top: 0;
          right: 0;
          transform: translateX(20px);
        }
        .sublist-2 {
          position: absolute;
          top: 0;
          right: -205%;
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          gap: 20px;
          display: none;
          .sublist-2-title {
            font-family: var(--font-family);
            font-weight: 700;
            font-size: 18px;
            line-height: 120%;
            text-transform: uppercase;
            color: var(--chernyy);
            margin-bottom: 10px;
          }

          .sublist-2-menu {
            display: flex;
            flex-direction: column;
            gap: 12px;

            a {
              font-family: var(--font-family);
              font-weight: 600;
              font-size: 14px;
              color: var(--chernyy);
              transition: color 0.3s ease;
              padding: 0;
              @include hover {
                &:hover {
                  color: var(--siniy);
                }
              }
            }
          }
        }

        @media (any-hover: hover) {
          &:hover {
            .sublist-2 {
              display: grid;
            }
          }
        }
      }
      &__link {
        border-radius: 8px;
        padding: 15px 20px;
        background: var(--fon);
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        color: var(--chernyy);
        position: relative;
        transition: 0.3s ease;
        width: 100%;
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          right: 20px;
          width: 8px;
          height: 12px;
          background: url("../../img/icons/arrow-link-menu.svg") no-repeat;
        }

        @include hover {
          &:hover {
            background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
          }
        }
      }
    }
  }

  .bottom {
    background: linear-gradient(132deg, #e3e8f3 0%, #c1d2f6 100%);
    padding-block: 15px;

    &__menu {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      .nav {
        .list {
          display: flex;
          align-items: center;
          li {
            position: relative;

            @include hover {
              &:hover {
                &:has(.sublist__wrapper) {
                  .sublist__wrapper {
                    display: block;
                  }
                }
              }
            }

            &:has(.sublist__wrapper) {
              &:after {
                content: "";
                position: absolute;
                height: 60px;
                width: 100%;
                bottom: -30px;
                left: 0;
              }
            }
          }
          a {
            border-radius: 8px;
            padding: 10px 20px;
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 14px;
            text-transform: uppercase;
            color: var(--chernyy);
            display: flex;
            align-items: center;
            gap: 8px;
            @media (max-width: 1344px) {
              padding: 5px 10px;
            }
            &.active {
              background: var(--siniy);
              color: var(--belyy);
            }
          }
        }
      }
    }
  }
}

.header-desktop {
  @media (max-width: 1200px) {
    display: none;
  }
}

.header-mobile {
  padding-bottom: 10px;
  display: none;
  @media (max-width: 1200px) {
    display: block;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;
  }

  &__logo {
    flex: 1 1 auto;
    margin-right: 20px;
  }

  &__language {
    border-radius: 4px;
    background: var(--fon);
    width: 60px;
    height: 40px;
    display: grid;
    place-items: center;

    &-btn {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--chernyy);
      position: relative;
      padding-left: 28px;
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 21px;
        height: 15px;
      }

      &.rus:before {
        background: url("../../img/icons/rus-flag.svg") no-repeat;
      }
    }
  }

  &__search {
    border-radius: 4px;
    width: 40px;
    height: 40px;
    background: var(--fon);
    display: grid;
    place-items: center;
  }

  &__phone {
    border-radius: 4px;
    width: 40px;
    height: 40px;
    border: 1px solid var(--siniy);
    display: grid;
    place-items: center;
  }

  .burger {
    width: 40px;
    height: 40px;
    display: grid;
    place-items: center;
    background: var(--siniy);
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    & > * {
      pointer-events: none;
    }
    &-wrapper {
      position: relative;
      display: flex;
      flex-direction: column;
      gap: 4px;
      width: 20px;
      height: 14px;
    }

    span {
      width: 20px;
      height: 1px;
      background: #fff;
      transition: all 0.3s ease;
      transform-origin: center;

      &:nth-child(1) {
        width: 13px;
      }
      &:nth-child(2) {
        width: 16px;
      }
      &:nth-child(3) {
        width: 18px;
      }
    }

    &--active {
      .burger-wrapper {
        span {
          width: 20px;

          &:nth-child(1) {
            transform: translateY(6px) rotate(45deg);
          }

          &:nth-child(2) {
            opacity: 0;
            transform: scale(0);
          }

          &:nth-child(3) {
            transform: translateY(-4px) rotate(-45deg);
          }
        }
      }
    }
  }

  &__menu {
    position: absolute;
    top: 66px;
    left: -100%;
    opacity: 0;
    visibility: hidden;
    width: 100%;
    height: 100vh;
    padding-inline: 20px;
    padding-top: 20px;
    backdrop-filter: blur(20px);
    z-index: 11;
    transition: 0.3s ease;
    background: rgba(255, 255, 255, 0.5);
    overflow: auto;
    padding-bottom: 20%;
    &.active-menu {
      opacity: 1;
      left: 0;
      visibility: visible;
    }
    .mobile-menu {
      margin-bottom: 20px;
      .list {
        display: flex;
        flex-direction: column;
        gap: 4px;
      }

      a {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        font-family: var(--font-family);
        font-weight: 500;
        font-size: 14px;
        text-transform: uppercase;
        color: var(--chernyy);
        padding: 12px 8px;
        background: #fff;
        border-radius: 8px;

        &.mobile-menu-item-accent {
          background: var(--siniy);
          color: #fff;
        }
      }
    }
  }

  &__footer {
    display: flex;
    flex-direction: column;
    gap: 10px;

    .compare {
      position: relative;
      border-radius: 8px;
      padding: 8px 20px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      font-family: var(--font-family);
      font-weight: 400;
      font-size: 16px;
      line-height: 1;
      color: var(--chernyy);
      gap: 5px;
      max-width: 420px;
      margin: 0 auto;
      width: 100%;
      span {
        position: absolute;
        top: 3px;
        left: 35%;
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 12px;
        line-height: 90%;
        color: var(--belyy);
        border-radius: 2px;
        padding: 2px 6px;
        background: var(--siniy);
      }
    }

    &-contacts {
      display: flex;
      flex-direction: column;
      gap: 10px;
      margin-bottom: 11px;
      .email {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 130%;
        color: var(--chernyy);
      }

      .socials {
        display: flex;
        align-items: center;
        gap: 20px;

        .socials-wrapper {
          display: flex;
          gap: 4px;
        }
      }

      .phone {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        text-transform: uppercase;
        color: var(--chernyy);
      }
    }
  }

  .city-select {
    margin-bottom: 20px;
  }
}
