.hero {
  margin-top: 10px;
  margin-bottom: 60px;

  &__slider {
    overflow: hidden;
    border-radius: 8px;
  }

  &__slide {
    position: relative;
    width: 100%;
    box-sizing: border-box;
  }

  &__bg {
    position: absolute;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      border-radius: 8px;
      object-fit: cover;
    }
  }

  .content {
    max-width: 600px;
    position: relative;
    z-index: 1;

    margin-left: auto;
    padding: 30px 40px;
    @media (max-width: 767px) {
      padding: 10px 20px;
      height: 230px;
      padding: 0;
      max-width: 100%;
    }
    &:after {
      content: "";
      position: absolute;
      inset: 0;
      background: #289b6b;
      transform: skewX(-20deg);
      right: -60px;

      @media (max-width: 767px) {
        display: none;
      }
    }

    .content-wrapper {
      display: flex;
      flex-direction: column;
      align-items: end;
      justify-content: end;
      position: relative;
      z-index: 1;
      @media (max-width: 767px) {
        align-items: center;
        justify-content: center;
        text-align: center;
        padding: 10px 20px;
        background: #289b6b;
      }
    }
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 700;
    font-size: clamp(20px, 3vw, 32px);
    text-transform: uppercase;
    text-align: right;
    color: var(--belyy);
    margin: 0;
    margin-bottom: 10px;
    @media (max-width: 767px) {
      text-align: center;
    }
  }

  &__desc {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    text-align: right;
    color: var(--belyy);
    margin: 0;
    margin-bottom: 20px;

    @media (max-width: 767px) {
      text-align: center;
    }
  }

  .list {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    max-width: 370px;
    margin-bottom: 34px;
    @media (max-width: 767px) {
      display: none;
    }
    li {
      font-family: var(--font-family);
      font-weight: 600;
      font-size: 14px;
      color: var(--belyy);
      border-radius: 8px;
      padding: 8px 10px;
      box-shadow: 0 4px 13px 0 rgba(30, 55, 91, 0.14);
      background: var(--divayder-belyy);
      flex: 1 1 225px;
      text-align: center;

      &:nth-child(2) {
        max-width: 134px;
      }
    }
  }

  &__btn {
    background: var(--belyy);
    color: var(--chernyy);
    font-weight: bold;
    padding: 12px 40px;
    @include hover {
      &:hover {
        color: var(--belyy);
      }
    }

    @media (max-width: 767px) {
      padding: 8px 35px;
      max-width: 100%;
      width: 100%;
    }
  }
}
