.partners {
  margin-bottom: 60px;
  &__title {
    margin-bottom: 30px;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 10px;
    @media (max-width: 992px) {
      grid-template-columns: 1fr 1fr 1fr;
    }
    @media (max-width: 767px) {
      grid-template-columns: 1fr 1fr;
    }
  }
}
