.cb {
  margin-bottom: 30px;
  h1 {
    margin-bottom: 10px;
  }
  p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin: 0;
  }

  .banner-section {
    padding-bottom: 76px;
  }
}

.cb-form {
  margin-bottom: 60px;
  &__wrapper {
    border-radius: 8px;
    padding: 40px 20px;
    background: var(--fon);
  }

  .form-cb {
    max-width: 1010px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    gap: 20px;

    &__row {
      display: flex;
      flex-direction: column;
      gap: 10px;

      p {
        font-family: var(--font-family);
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: var(--chernyy);
        margin: 0;
        margin-bottom: 4px;
      }
    }

    &__fields {
      display: flex;
      flex-wrap: wrap;
      gap: 10px;

      input {
        flex: 1 1 330px;
      }
    }

    &__field {
      border-radius: 8px;
      padding: 12px 8px;
      flex: 1 1 330px;
      background: var(--belyy);
      position: relative;
      input {
        position: absolute;
        inset: 0;
        z-index: 1;
        opacity: 0;
        appearance: none;
        margin: 0;
        cursor: pointer;
      }
      label {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        color: var(--chernyy);
        display: block;
        position: relative;
        padding-left: 24px;
        &:after {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
          border: 1px solid #000;
          width: 18px;
          height: 18px;
          border-radius: 100%;
          transition: 0.3s ease;
        }
      }

      input:checked + label:after {
        background: url("../../img/icons/radio-checked.svg") center / cover
          no-repeat;
        border-color: transparent;
      }
    }
    textarea {
      height: 90px;
    }

    &__bottom {
      display: flex;
      align-items: center;
      gap: 20px;
      flex-wrap: wrap;
      .policy {
        display: flex;
        flex-direction: column;
        gap: 8px;
        max-width: 325px;
        width: 100%;
        .policy-block {
          position: relative;
          .checkbox {
            position: absolute;
            opacity: 0;
            appearance: none;
            width: 100%;
            height: 100%;
            cursor: pointer;
            z-index: 1;
          }
          label {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            color: var(--chernyy);
            position: relative;
            display: block;
            padding-left: 24px;
            a {
              text-decoration: underline;
              text-decoration-skip-ink: none;
              color: inherit;
              position: relative;
              z-index: 2;
            }

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 14px;
              height: 14px;
              border-radius: 2px;
              border: 1px solid var(--siniy);
              position: absolute;
              transition: 0.3s ease;
            }
          }

          .checkbox:checked + label:before {
            background: url("../../img/icons/checkbox.svg") no-repeat;
            border-color: transparent;
          }
        }
      }
      .btn--accent {
        border-radius: 8px;
        padding: 12px 40px;
        width: 100%;
      }
    }
  }
}
