.before-footer {
  background: linear-gradient(132deg, #c1d2f6 0%, #e3e8f3 100%);
  padding-top: 80px;
  margin-top: 120px;
  padding-bottom: 40px;

  @media (max-width: 992px) {
    padding-bottom: 0;
  }
  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    @media (max-width: 992px) {
      flex-direction: column;
    }
  }

  p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 130%;
    color: var(--chernyy);
    margin: 0;
    margin-bottom: 30px;
    margin-top: 8px;
  }

  .footer-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
    z-index: 1;
    &__block {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 10px;
      @media (max-width: 992px) {
        grid-template-columns: 1fr;
      }
    }
    &__bottom {
      margin-top: 20px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 10px;
      @media (max-width: 992px) {
        grid-template-columns: 1fr;
      }
      .policy {
        display: flex;
        flex-direction: column;
        gap: 8px;
        max-width: 325px;
        width: 100%;
        .policy-block {
          position: relative;
          .checkbox {
            position: absolute;
            opacity: 0;
            appearance: none;
            width: 100%;
            height: 100%;
            cursor: pointer;
            z-index: 1;
          }
          label {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 12px;
            color: var(--chernyy);
            position: relative;
            display: block;
            padding-left: 24px;
            a {
              text-decoration: underline;
              text-decoration-skip-ink: none;
              color: inherit;
              position: relative;
              z-index: 2;
            }

            &:before {
              content: "";
              position: absolute;
              top: 0;
              left: 0;
              width: 14px;
              height: 14px;
              border-radius: 2px;
              border: 1px solid var(--siniy);
              position: absolute;
              transition: 0.3s ease;
            }
          }

          .checkbox:checked + label:before {
            background: url("../../img/icons/checkbox.svg") no-repeat;
            border-color: transparent;
          }
        }
      }
      .btn--accent {
        border-radius: 8px;
        padding: 12px 40px;
        width: 100%;
      }
    }
  }

  &__image {
    position: absolute;
    bottom: -40px;
    right: 0;
    max-width: 480px;
    @media (max-width: 992px) {
      position: static;
    }
    img {
      display: block;
    }
  }
}

.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(50px);
  opacity: 0;
  visibility: hidden;
  transition: 0.3s ease;

  &._active {
    opacity: 1;
    visibility: visible;
  }
}
