.faq {
  &__wrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    gap: 40px;

    @media (max-width: 992px) {
      grid-template-columns: 1fr;
    }
  }

  &__aside {
    @media (max-width: 992px) {
      display: none;
    }
  }

  .banner-section {
    margin-bottom: 10px;
  }

  &__callback {
    border-radius: 8px;
    background: var(--fon);
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 30px;
    p {
      margin: 0;
      font-family: var(--font-family);
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      text-transform: uppercase;
      color: var(--chernyy);
    }
    .btn--accent {
      padding: 12px 40px;
    }
  }

  .questions {
    &__list {
      display: flex;
      flex-wrap: wrap;
      gap: 4px;
      margin-bottom: 20px;
      &-btn {
        border-radius: 8px;
        padding: 8px 20px;
        background: var(--fon);
        color: var(--chernyy);
        @include hover {
          &:hover {
            background: var(--siniy);
            color: var(--belyy);
          }
        }

        &.active {
          background: var(--siniy);
          color: var(--belyy);
        }
      }
    }

    &__items {
      display: grid;
      gap: 10px;
    }

    &__item {
      border-radius: 8px;
      padding: 14px 20px;
      background: var(--fon);
      cursor: pointer;
      & > * {
        pointer-events: none;
      }
      .head {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        align-items: center;

        .title {
          font-family: var(--font-family);
          font-weight: 700;
          font-size: 16px;
          line-height: 120%;
          color: var(--chernyy);
        }

        .toggler {
          border: 1px solid var(--siniy);
          border-radius: 4px;
          width: 30px;
          height: 30px;
          display: grid;
          place-items: center;

          svg {
            transition: 0.3s ease;
          }
        }
      }

      .bottom {
        display: grid;
        grid-template-rows: 0fr;
        overflow: hidden;
        transition: 0.3s ease;

        .content {
          overflow: hidden;
          margin-top: 0;
          transition: 0.3s ease;
          font-family: var(--font-family);
          font-weight: 400;
          font-size: 14px;
          line-height: 130%;
          color: var(--chernyy);
        }
      }
    }

    .questions__item--active {
      .bottom {
        grid-template-rows: 1fr;
      }
      .content {
        margin-top: 20px;
      }

      .toggler svg {
        rotate: 45deg;
      }
    }
  }
}
